@import "../../style/mixins";

$background-color--primary: #1E1E1D;
$background-color--secondary: #565A5C;
$background-color--tertiary: #707476;
$font-color--primary: #ffffff;
$font-color--secondary: #eeeeee;

$red: #c60c30;
$blue: #00a1de;
$brown: #62361b;
$green: #009b3a;
$orange: #f9461c;
$purple: #522398;
$pink: #e27ea6;
$yellow: #f9e300;

$gradiant-transition-time: 8s;

@mixin gradiant-background($color1, $color2, $transition-time) {

	background: linear-gradient(270deg, $color1, $color2);
	background-size: 200% 200%;

	-webkit-animation: AnimationName $transition-time ease infinite;
	-moz-animation: AnimationName $transition-time ease infinite;
	animation: AnimationName $transition-time ease infinite;

	@-webkit-keyframes AnimationName {
		0%{background-position:0% 50%}
		50%{background-position:100% 50%}
		100%{background-position:0% 50%}
	}
	@-moz-keyframes AnimationName {
		0%{background-position:0% 50%}
		50%{background-position:100% 50%}
		100%{background-position:0% 50%}
	}
	@keyframes AnimationName {
		0%{background-position:0% 50%}
		50%{background-position:100% 50%}
		100%{background-position:0% 50%}
	}

}

@mixin train-background($routeColor) {
	background-color: rgba($routeColor, 0.50);
}

.ctaApp {
	width: 100vw;
	height: 100vh;
	overflow-y: scroll;
	@include gradiant-background($red, $blue, $gradiant-transition-time);
	color: $font-color--primary;
	padding: 0.5rem;

	@include font-family(regular);

	&ShowTrains {
		@include gradiant-background($background-color--primary, $background-color--secondary, $gradiant-transition-time);

		.ctaAppStopsBuses {
			display: none;
		}
	}

	&ShowBuses {
		.ctaAppStopsTrains {
			display: none;
		}
	}

	&Child {
		margin-bottom: 0.5rem;
		padding: 0.5rem;
		background-color: rgba(white, 0.15);
		backdrop-filter: blur(5px);

		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;

		@include breakAt(tablet){
			width: calc(50% - 0.25rem);
			display: inline-flex;

			&:nth-child(odd) {
				margin-right: 0.5rem;
			}
		}

		&:first-of-type {
			margin-top: 0;
		}

		&Train {

			&Red {
				@include train-background($red);
			}
			&Blue {
				@include train-background($blue);
			}
			&Brown {
				@include train-background($brown);
			}
			&Green {
				@include train-background($green);
			}
			&Orange {
				@include train-background($orange);
			}
			&Purple {
				@include train-background($purple);
			}
			&Pink {
				@include train-background($pink);
			}
			&Yellow {
				@include train-background($yellow);
			}
		}

		&Left {
			word-break: break-word;
		}

		&Right {
			text-align: right;
			padding-left: 1rem;
		}

		&Large {
			font-size: 1rem;
			@include font-family(bold);

			@include breakAt(mobile){
				font-size: 18px;
			}
			@include breakAt(tablet){
				font-size: 24px;
			}
		}
		&Small {
			font-size: 0.875rem;
			color: $font-color--secondary;

			@include breakAt(tablet){
				font-size: 1rem;
			}
		}
	}

	&Controls {
		display: flex;
		flex-direction: row;
		margin-bottom: 0.5rem;

		&Btn{
			display: block;
			margin: 0;
			width: auto;
			overflow: visible;

			line-height: normal;
			-webkit-font-smoothing: inherit;
			-moz-osx-font-smoothing: inherit;
			-webkit-appearance: none;

			@include train-background($background-color--secondary);
			color: white;

			border: 1px solid $font-color--secondary;

			padding: 0.5rem;
			margin-right: 0.5rem;
			&:last-of-type {
				margin-right: 0;
			}

			// TODO Refactor font utility in mixin
			@include font-family(regular);

			font-size: 1rem;
			@include font-family(bold);
			@include breakAt(mobile){
				font-size: 18px;
			}
			@include breakAt(tablet){
				font-size: 24px;
			}

			&:hover {
				cursor: pointer;

				background-color: $background-color--secondary;
				color: white;
			}

			&Active {
				background-color: $font-color--secondary;
				color: $background-color--secondary;

				&:hover {
					background-color: $font-color--secondary;
					color: $background-color--secondary;
				}
			}

			&Train, &Bus {
				flex-grow: 2;
			}

			&Refresh {
				position: relative;
				flex-grow: 1;

				transition: border 0.5s ease-in;

				&Loading {

				}

				&JustLoaded {
					border: 1px solid #009b3a;

					.ctaAppControlsBtnRefreshProgress {
						background-color: #009b3a;
					}
				}

				&Loaded, &Ready {


					.ctaAppControlsBtnRefreshProgress {
						opacity: 0;
					}
				}

				&Loading, &JustLoaded, &Loaded {
					cursor: auto;
					pointer-events: none;
				}

				&Ready {

				}

				&Text {
					position: relative;
					text-align: center;
					z-index: 100;
				}

				&Progress {
					position: absolute;
					display: block;
					z-index: 1;
					top: 0;
					left: 0;
					height: 100%;
					opacity: 1;
					background-color: $background-color--tertiary;

					transition: all 0.5s ease-in;
				}
			}
		}
	}
}
